import React from "react";
import { Link } from "gatsby";
import Img from "gatsby-image";
import styled from "styled-components";
import { media } from "../../helpers/media-queries";
import { LinkUnstyled, ReadMoreButton } from "./PostLink";

const PostSummary = ({ post }) => {
  const featuredImgFluid =
    post.frontmatter.featuredImageSummary.childImageSharp.fluid;
  return (
    <PostContainer className={"post-container"}>
      <ImgWrapper>
        <LinkUnstyled to={post.frontmatter.slug}>
          <ImgStyled
            fluid={featuredImgFluid}
            style={{ "--bgcolor": post.frontmatter.bgcolor }}
          />
        </LinkUnstyled>
      </ImgWrapper>

      <BlockWrapper>
        <LinkUnstyled to={post.frontmatter.slug}>
          <PostTitle>{post.frontmatter.title}</PostTitle>
        </LinkUnstyled>
        <PostContent>
          {post.frontmatter.author}
          <Separator />
          {post.frontmatter.date}
        </PostContent>
        <div>
          <ReadMoreButton to={post.frontmatter.slug}>
            Read article
          </ReadMoreButton>
        </div>
      </BlockWrapper>
    </PostContainer>
  );
};
export default PostSummary;

const PostContainer = styled(Link)`
  margin-bottom: 30px;
  display: flex;
  flex-direction: column;
  text-decoration: none;

  ${media.m`
    flex-direction: row;
    gap: 24px;
  `}
`;

const ImgWrapper = styled.div`
  flex: 1 0 auto;
  display: flex;
  justify-content: center;
  ${media.m`
    max-width: 260px;
    max-height: 260px;
  `}

  & > * {
    flex: 1 0 auto;
  }
`;

const BlockWrapper = styled.div`
  flex: 0 1 calc(100% - 260px);
`;

const ImgStyled = styled(Img)`
  max-width: 260px;
  overflow: visible !important;

  .post-container &::before {
    width: 50%;
    content: "";
    position: absolute;
    background-color: var(--bgcolor, #4200ff);
    left: 0;
    bottom: -6px;
    height: 6px;

    ${media.l`
      bottom: -8px;
      height: 8px;
    `}
    ${media.xl`
      bottom: -10px;
      height: 10px;
    `}
  }
`;

const PostTitle = styled.h2`
  font-family: "Poppins";
  font-size: 20px;
  line-height: 24px;
  font-weight: 500;

  margin-top: 26px;
  margin-bottom: 4px;
  color: #47474f;

  /* Multiline firefox and webkit */
  text-overflow: ellipsis;
  overflow: hidden;
  display: -webkit-box !important;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;

  ${media.m`
    margin-top: 0;
  `}

  ${media.l`
    margin-bottom: 8px;
    font-size: 26px;
    line-height: 33.8px;
  `}
  ${media.xl`
    font-size: 38px;
    line-height: 49.21px;
  `}
`;

const PostContent = styled.p`
  font-family: "Roboto";
  font-size: 12px;
  font-weight: 400;
  line-height: 28px;

  margin-bottom: 18px;
  color: #747487;

  ${media.xl`
  margin-bottom: 22px;
  `}
`;

export const Separator = styled.span`
  display: inline-block;
  margin: 3px 8px;
  background-color: #747487;
  width: 4px;
  height: 4px;
  transform: rotate(45deg);
`;
